/*
 *
 * DeveloperAccountDetailPage
 *
 */

import React from 'react';
import { connect } from 'react-redux';
import {
  Form,
  FormGroup,
  Input,
  Label,
  Col,
  Container,
  InputProps,
} from 'reactstrap';
import Helmet from 'react-helmet';
import { FormattedMessage } from 'react-intl';
import { selectUserInfo } from './selectors';
import messages from './messages';
import ConsolePage from 'components/ConsolePage';
import ConsolePageHeader from 'components/ConsolePageHeader';
import landingPageMessages from 'components/LandingRegister/messages';
import CopyInput from 'components/CopyInput';
import { getDevTokenFromStorage } from 'cb-utils/devToken';
import { createStructuredSelector } from 'reselect';

interface IInputProps extends Partial<InputProps> {
  'data-cy'?: string;
}

const createRow = (
  msg: FormattedMessage.MessageDescriptor,
  value?: string,
  customInput?: JSX.Element,
  inputProps: IInputProps = {},
) => (
  <FormGroup row>
    <Label sm={2}>
      <FormattedMessage {...msg} />
    </Label>
    <Col sm={10}>
      {customInput ? (
        customInput
      ) : (
        <Input
          type="text"
          readOnly
          {...inputProps}
          className="form-control-plaintext"
          value={value}
        />
      )}
    </Col>
  </FormGroup>
);

export class DeveloperAccountDetailPage extends React.PureComponent<
  StateToProps,
  {}
> {
  devToken = getDevTokenFromStorage();

  render() {
    const { userInfo } = this.props;
    return (
      <div>
        <Helmet
          title="Account Detail"
          meta={[
            { name: 'description', content: 'Description of Account Detail' },
          ]}
        />
        <ConsolePage
          subheader={
            <ConsolePageHeader
              header={<FormattedMessage {...messages.header} />}
            />
          }
        >
          <Container>
            {userInfo && (
              <Form onSubmit={(e) => e.preventDefault()}>
                {createRow(landingPageMessages.email, userInfo.email, null, {
                  'data-cy': 'devUserEmail',
                })}
                {createRow(landingPageMessages.fName, userInfo.fname)}
                {createRow(landingPageMessages.lName, userInfo.lname)}
                {createRow(landingPageMessages.org, userInfo.org)}
                {createRow(messages.created, userInfo.creation_date)}
                {createRow(messages.lastLogin, userInfo.last_login)}
                {createRow(
                  messages.devToken,
                  null,
                  <CopyInput value={this.devToken} readOnly />,
                )}
              </Form>
            )}
          </Container>
        </ConsolePage>
      </div>
    );
  }
}

type StateToProps = ReturnType<typeof mapStateToProps>;

const mapStateToProps = createStructuredSelector({
  userInfo: selectUserInfo(),
});

export default connect<StateToProps>(mapStateToProps, null)(
  DeveloperAccountDetailPage,
);



