import { createSelector } from 'reselect';
import { selectCurrentUser } from '../Console/selectors/currentUser';
import { format } from 'date-fns';
import { DevUserModel } from 'cb-utils/console-entity-models';

export interface IAccountDetailDevModel
  extends Pick<DevUserModel, 'email'>,
    Pick<DevUserModel, 'fname'>,
    Pick<DevUserModel, 'lname'>,
    Pick<DevUserModel, 'org'> {
  last_login: string;
  creation_date: string;
}

const formatDate = (secs: number) =>
  format(new Date(secs * 1000), `MMM DD YYYY, hh:mm a`);

const selectUserInfo = () =>
  createSelector(
    selectCurrentUser(),
    (substate) =>
      substate
        ? {
            ...substate,
            last_login: formatDate(substate.last_login),
            creation_date: formatDate(substate.creation_date),
          }
        : null,
  );

export { selectUserInfo };



